export default {
	props: [
		"list"
	],
	data() {
		return {
			activeTab: 0
		};
	},
	methods: {
		// 切换标签时，高亮显示当前选中标签
		currentTab(item, index) {
			this.activeTab = index
			// 将当前选中的标签索引传递给gesture组件
			this.$parent.$children[2].cut(index)
		},
		// 手势切换卡片视图区域时，设置高亮标签
		getActiveIndex(index) {
			this.activeTab = index
		},
		// 跳转到标签管理页
		toLabelPage(){
			uni.navigateTo({
				url:'../../../pages/label/label'
			})
		}
			
	}
}
